import { Module, MutationTree, ActionTree, GetterTree } from 'vuex';

const API_URL = 'https://sunnytseng.com/api/relation/posts/'; // Deploy to server
// const API_URL = '/api/relation/posts/';

// 1. å¾Œç«¯å›žå‚³ User ç‰©ä»¶
export interface User {
  id: number;
  account: string;
  name: string;
  avatar: string;
}

// 2. å¾Œç«¯çš„ media è‹¥éž null å°±æ˜¯ä¸€å€‹é™£åˆ—ï¼Œå«å¤šå€‹ { url, type }
export interface MediaItem {
  url: string;
  type: string;
}

// 3. è·Ÿå¾Œç«¯ä¸€è‡´çš„ Post ä»‹é¢
export interface Post {
  id: string;
  user: User;                  // user æ•´å€‹ç‰©ä»¶
  username: string;            // é¡å¤–çš„æ¬„ä½
  content: string;
  media: MediaItem[] | null;   // å¯èƒ½æ˜¯ null æˆ–é™£åˆ—
  created_at: string;          // å¾Œç«¯æ˜¯ created_at
  likes: number;
  retweets: number;
  replies: number;
  is_liked: boolean;           // å¾Œç«¯æ˜¯ is_liked
  is_retweeted: boolean;       // å¾Œç«¯æ˜¯ is_retweeted
  parent_post: string | null;  // å¯èƒ½ç”¨ä¸åˆ°ï¼Œä½†å¾Œç«¯æœ‰å‚³
}

// 4. å¾Œç«¯åˆ—è¡¨åž‹æ…‹
export interface PostListResponse {
  count: number;
  next: string | null;
  previous: string | null;
  results: Post[];
}

// 5. Vuex ä¸­çš„ stateï¼Œå¯ä»¥å„²å­˜å¾Œç«¯çš„ postsï¼Œä»¥åŠå…¶ä»–ç™»å…¥è³‡è¨Š
export interface PostState {
  posts: Post[];         // å­˜æ”¾å¾Œç«¯å›žå‚³çš„é™£åˆ—
  account: string;
  userName: string;
  token: string | null;
}

// 6. stateï¼šåˆå§‹åŒ–
const state: PostState = {
  posts: JSON.parse(localStorage.getItem('posts') || '[]'),
  account: localStorage.getItem('account') || '',
  userName: localStorage.getItem('userName') || '',
  token: localStorage.getItem('token') || null,
};

// 7. Getter
const getters: GetterTree<PostState, any> = {
  getAllPosts: (state) => state.posts,
  getPostById:
    (state) =>
    (id: string) =>
      state.posts.find((post) => post.id === id),
};

// 8. Mutation
const mutations: MutationTree<PostState> = {
  // æŠŠå¾Œç«¯æ•´å€‹åˆ—è¡¨ (results) å¯«é€² state
  SET_POSTS(state, data: PostListResponse) {
    state.posts = data.results;
  },

  ADD_POST(state, newPost: Post) {
    state.posts.unshift(newPost);
  },

  LIKE_POST(state, postId: string) {
    const post = state.posts.find((p) => p.id === postId);
    if (post) {
      // è·Ÿå¾Œç«¯æ¬„ä½ä¸€è‡´ï¼šis_liked
      post.is_liked = !post.is_liked;
      post.likes += post.is_liked ? 1 : -1;
    }
  },

  RETWEET_POST(state, postId: string) {
    const post = state.posts.find((p) => p.id === postId);
    if (post) {
      post.is_retweeted = !post.is_retweeted;
      post.retweets += post.is_retweeted ? 1 : -1;
    }
  },

  DELETE_POST(state, postId: string) {
    state.posts = state.posts.filter((post) => post.id !== postId);
  },
};

// 9. Action
const actions: ActionTree<PostState, any> = {
  // 9-1. å–å¾—æ‰€æœ‰è²¼æ–‡
  // async fetchPosts({ commit, state }) {
  async fetchPosts({ commit, rootState, getters }) {
    try {
      const token = rootState.auth.token || getters["auth/isAuthenticated"]; 
      // if (!state.token) {
      if (!token) {
        throw new Error("No authentication token found");
      }

      const response = await fetch(API_URL, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        throw new Error(`Failed to fetch posts: ${response.status} ${response.statusText}`);
      }

      const data: PostListResponse = await response.json();
      commit('SET_POSTS', data);
    } catch (error) {
      console.error("Error fetching posts:", error);
    }
  },

  // 9-2. æ–°å¢žè²¼æ–‡ (POST)
  //    ç”±æ–¼æˆ‘å€‘ç¾åœ¨çš„ Post interface æœ‰å¾ˆå¤šå¾Œç«¯è‡ªå‹•å¸¶çš„æ¬„ä½ (ä¾‹å¦‚ id, created_at)ï¼Œ
  //    å‰ç«¯åœ¨ç™¼é€æ™‚é€šå¸¸åªéœ€è¦ contentã€æˆ–é™„å¸¶ media (binary)ã€‚
  //    é€™è£¡è¦–å¾Œç«¯ API æ ¼å¼è€Œå®šï¼Œçœ‹æ˜¯å¦è¦å‚³ body: { content }ã€‚
  //    ä»¥ä¸‹åƒ…ä¾›åƒè€ƒå¯«æ³•ï¼š
  
  async createPost(
    { commit, rootState },
    payload: { content: string; images: File[] }
  ) {
    try {
      const token: string | null = rootState.auth.token;
      if (!token) throw new Error("Please login");

      const formData = new FormData();
      formData.append("text", payload.content);

      payload.images.forEach((file) => {
        formData.append("images", file);
      });

      const response = await fetch(API_URL, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`, // Remove 'Content-Type' since FormData sets it automatically
        },
        body: formData,
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`Failed to create post: ${errorText}`);
      }

      const newPost: Post = await response.json();
      commit("ADD_POST", newPost);
    } catch (error) {
      console.error("Error creating post:", error);
      throw error;
    }
  },

  // 9-3. æŒ‰è®š (PUT)
  //    å‡è¨­å¾Œç«¯ API æ˜¯ /api/relation/posts/:postId/like
  //    ä¸¦ä¸”å¾Œç«¯æœƒä¿®æ”¹ is_liked / likes æ•¸é‡
  async likePost({ commit }, postId: string) {
    try {
      const response = await fetch(`${API_URL}${postId}/like`, {
        method: 'PUT',
      });
      if (!response.ok) throw new Error('Failed to like post');
      commit('LIKE_POST', postId);
    } catch (error) {
      console.error('Error liking post:', error);
    }
  },

  // 9-4. è½‰æŽ¨ (PUT)
  async retweetPost({ commit }, postId: string) {
    try {
      const response = await fetch(`${API_URL}${postId}/retweet`, {
        method: 'PUT',
      });
      if (!response.ok) throw new Error('Failed to retweet post');
      commit('RETWEET_POST', postId);
    } catch (error) {
      console.error('Error retweeting post:', error);
    }
  },

  // 9-5. åˆªé™¤è²¼æ–‡ (DELETE)
  async deletePost({ commit }, postId: string) {
    try {
      const response = await fetch(`${API_URL}${postId}`, {
        method: 'DELETE',
      });
      if (!response.ok) throw new Error('Failed to delete post');
      commit('DELETE_POST', postId);
    } catch (error) {
      console.error('Error deleting post:', error);
    }
  },
};

// 10. åŒ¯å‡ºé€™å€‹ Module
export const postModule: Module<PostState, any> = {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};
